import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/todo',
    name: 'Home',
    component: Home,
  },
  {
    path: '/todo/notes/:id',
    name: 'NoteView',
    component: () => import('../views/NoteView.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
